import { Component, OnInit } from '@angular/core';
import { Login } from '../modals';
import { ForgotPasswordService } from './forgot-password.service';
import { ToastrService } from 'ngx-toastr';
import { GlobalService } from './../../../shared-ui/global.service';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  loading: boolean;
  errorMessage: string;
  successMessage: string;
  userData: Login = {
    email: ''
  };

  constructor(
    private forgotPasswordService: ForgotPasswordService,
    private toastr: ToastrService,
    private globalService: GlobalService
  ) {

  }

  ngOnInit() {
  }

  emailLink: String = '';
  forgotPassword() {
    this.loading = true;
    this.emailLink = '';
    this.userData.email = this.userData.email.toLocaleLowerCase();
    this.forgotPasswordService.forgotPassword(this.userData).subscribe(
      data => {
        if (data.status === 200) {
          this.emailLink = data.emailLink;
          this.toastr.success(
            'Please check your email, Reset password link has been sent.',
            'Success'
          );
          this.errorMessage = '';
          this.successMessage = data.message;
          this.userData = {
            email: ''
          };
          this.loading = false;
        } else {
          this.toastr.error(data.message, 'Error');
          this.successMessage = '';
          this.errorMessage = data.message;
          this.userData = {
            email: ''
          };
          this.loading = false;
        }
      },
      error => {
        this.toastr.error(
          'There are some server Please check connection.',
          'Error'
        );
      }
    );
  }
  validEmail: Boolean = true;
  checkValidEmail(email?: string) {
    this.validEmail = this.globalService.ValidateEmail(email);
    return this.validEmail;
  }

}
